import { Component, OnInit, EventEmitter, Output } from '@angular/core';

import { Event } from '../../classes/event';
import { EventService } from '../../angular-services/event.service';

@Component({
  selector: 'app-calendario',
  templateUrl: './calendario.component.html',
  styleUrls: ['./calendario.component.css']
})
export class CalendarioComponent implements OnInit {
  
  @Output() loading = new EventEmitter<boolean>();
  months = ['Janeiro', 'Fevereiro', 'Março', 'Abril', 'Maio', 'Junho', 'Julho', 'Agosto', 'Setembro', 'Outubro', 'Novembro', 'Dezembro'];
  monthSelected: number = new Date().getDate();
  yearSelected: number = new Date().getFullYear();
  eventsOfMonth: Event[] = [];
  events: Event[] = [];

  constructor(private eventService: EventService) { }

  ngOnInit() {
    this.getEvents();
  }

  getEventsOfMonth(month) {
    this.eventsOfMonth = [];
    this.monthSelected = month;
    for (let event of this.events) {
      if ((new Date(event.date).getMonth()) === month && new Date(event.date).getFullYear() === this.yearSelected) {
        this.eventsOfMonth.push(event);
      }
    }
    window.scrollTo(0, 150);
  }

  getEvents() {
    this.loading.emit(true);
    this.eventService.getEvents().subscribe(
      response => {
        this.events = response;
        this.getEventsOfMonth(new Date().getMonth());
        this.loading.emit(false);
      }
    );
  }

  previousYear() {
    this.yearSelected--;
    this.getEventsOfMonth(this.monthSelected);
  }

  nextYear() {
    this.yearSelected++;
    this.getEventsOfMonth(this.monthSelected);
  }

}
